.user {
  margin: 20px;
  left: 0;
  position: absolute;
}


#question {
  width: 500px;
  height: 300px;
}

.user button {
  margin-top: 10px;
  width: 500px;
  background-color: #D2B48C;
  color: #000;
  border-color: #8B4513;
  font-family: 'Trade Winds', serif;
  font-size: 16px;
}

.user button:hover {
  background-color: #8B4513;
  color: #D2B48C;
  border-color: #D2B48C;
}

.user textarea, .user textarea:focus {
  background: transparent;
  border:5px solid #8B4513;
}
.App {
  text-align: center;
}

h1 {
  margin-left: 100px;
}
